.App {
  text-align: center;
  height: 100vh;
  background-color: #61dafb;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: #61dafb;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.cool-logo{
  width: 30rem;
}

.container {
  padding: 3rem;
  min-height: 120px;
}

span {
  font-size: 3em;
  display: inline-block;
  overflow: hidden;
  position: relative;
}
.count-enter {
  -webkit-transition: 0.250s;
  -o-transition: 0.250s;
  transition: 0.250s;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
}

.count-enter-active {
  -webkit-transition: 0.250s;
  -o-transition: 0.250s;
  transition: 0.250s;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.count-exit {
  margin: 0 auto;
  -webkit-transition: 0.250s;
  -o-transition: 0.250s;
  transition: 0.250s;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  position: absolute;
  left: 0;
  bottom: 0;
}

.count-exit-active {
  -webkit-transition: 0.250s;
  -o-transition: 0.250s;
  transition: 0.250s;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}



